import api from '../http'

export const getBuyCarList = (params) => {
	return api.http({
		url:'/shop/buy-car/list',
		method:'get',
		params
	})
}

export const addBuyCar = (data) => {
	return api.http({
		url:'/shop/buy-car/insert',
		method:'put',
		data
	})
}

export const deleteBuyCarById = (id) => {
	return api.http({
		url:`/shop/buy-car/delete/id/${id}`,
		method:'delete'
	})
}